import React, { useRef, useState } from "react";
import { useDataProvider } from "../../provider/DataProvider";
import { useNavigate } from "react-router-dom";
import style from "./sign-up-page.module.css";
import { useTranslation } from "react-i18next";
const SignUpPage = () => {
  const navigate = useNavigate();
  const { addUser, isUserExist } = useDataProvider();
  const nameInp = useRef();
  const usernameInp = useRef();
  const passwordInp = useRef();
  const confirmPasswordInp = useRef();
  const errorDetails = [
    "id must  be 9 length",
    "name must be minimum 3 char ",
    "password must be minimum 5 char",
    "confirm password must be equal",
  ];
  const { t } = useTranslation();
  const [errorToggle, setErrorToggle] = useState(0);
  const inputs = [
    { type: "text", placeholder: "enter name", ref: nameInp },
    { type: "text", placeholder: "enter username", ref: usernameInp },
    { type: "password", placeholder: "enter password", ref: passwordInp },
    { type: "password", placeholder: "confirm password", ref: confirmPasswordInp },
  ];
  const checkInp = (username, name, password, confirmPassword) => {
    let checkIncorrectDetails = false;
    if (username.length <3) checkIncorrectDetails = true;

    if (name.length < 3) checkIncorrectDetails = true;

    if (password.length < 5) checkIncorrectDetails = true;

    if (confirmPassword !== password) checkIncorrectDetails = true;

    return checkIncorrectDetails;
  };
  const handleClick = (e) => {
    e.preventDefault();
    const name = nameInp.current.value;
    const username = usernameInp.current.value;
    const password = passwordInp.current.value;
    const confirmPassword = confirmPasswordInp.current.value;
    if (isUserExist(username)) return setErrorToggle(2);
    if (checkInp(username, name, password, confirmPassword)) return setErrorToggle(1);
    addUser(name, username, password);
    navigate("/sign-in");
  };
  return (
    <form className={style.form_container} onSubmit={handleClick}>
      <div>
        <h1 id={style.sign_up_tag}>{t("signup")}</h1>

        <div className={style.sign_up_container}>
          {inputs.map((inp, index) => {
            const { type, placeholder, ref, name } = inp;
            return (
              <input
                className={style.inp_sign_up}
                key={index}
                type={type}
                placeholder={placeholder}
                ref={ref}
                name={name}
              />
            );
          })}
          {errorToggle === 1 && (
            <div>
              {errorDetails.map((err) => {
                return <p className={style.error}>{err}</p>;
              })}
            </div>
          )}
          {errorToggle === 2 && <h2>user already exist</h2>}

          <button id={style.sign_up_button}>{t("signup")}</button>
        </div>
      </div>
    </form>
  );
};

export default SignUpPage;
