import React, { useRef } from "react";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { useDataProvider } from "../../provider/DataProvider";

import style from "./sign-in-page.module.css";
const SignInPage = () => {
  const {t} = useTranslation()
  const { specificUser } = useDataProvider();
  const navigate = useNavigate();
  const nameInp = useRef();
  const passwordInp = useRef();
  const [errorDetails, setErrorDetails] = useState("");
  const handleClick = (e) => {
    e.preventDefault();
    const name = nameInp.current.value;
    const password = passwordInp.current.value;
    if (name === "Admin" && password === "1111") return navigate("/admin");
    if (!specificUser(password, name)) return setErrorDetails(t("notfound"));
    (specificUser(password,name))
    navigate("/products", { state: specificUser(password, name) });
  };
  return (
    <form action="/shopping-cart/sign-in" method="get" onSubmit={handleClick} className={style.form_container}>
      <div>
        <h1 id={style.tag}>{t("signin")}</h1>
        <div className={style.sign_in_container}>
          <input className={style.inp} type="text" placeholder="enter name" ref={nameInp} />
          <input className={style.inp} type="password" placeholder="enter password" ref={passwordInp} />
          {errorDetails !== "" && <h2 id={style.errorDetails}>{errorDetails}!</h2>}
          <button id={style.sign_in_btn}>{t("signin")}</button>
        </div>
      </div>
    </form>
  );
};

export default SignInPage;
